import React, { Component } from 'react'

export default class Colors extends Component {
    SetColor=c=>{
        let Lc = this.props.litC
    }
  render() {
    return (
      <div>
        <ul>
            <li id="negro" onClick={this.SetColor()}>
                <span className="color"></span>
            </li>
            <li id="rojo">
                <span className="color"></span>
            </li>
            <li id="azul">
                <span className="color"></span>
            </li>
            <li id="amarillo">
                <span className="color"></span>
            </li>
        </ul>
      </div>
    )
  }
}
